<template>
  <view class="container">
    <!-- Your list of items -->
    <view class="item" v-for="(item, index) in paginatedItems" :key="index">
      <text>{{ item }}</text>
    </view>

    <!-- Pagination component -->
    <view class="pagination">
      <button @click="prevPage" :disabled="currentPage === 1">Previous</button>
      <span>Page {{ currentPage }} of {{ totalPages }}</span>
      <button @click="nextPage" :disabled="currentPage === totalPages">Next</button>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      items: [], // Your original list of items
      currentPage: 1,
      pageSize: 4 // Number of items per page
    };
  },
  computed: {
    // Calculate total pages based on the number of items and pageSize
    totalPages() {
      return Math.ceil(this.items.length / this.pageSize);
    },
    // Paginate the items list based on currentPage and pageSize
    paginatedItems() {
      const startIndex = (this.currentPage - 1) * this.pageSize;
      const endIndex = startIndex + this.pageSize;
      return this.items.slice(startIndex, endIndex);
    }
  },
  methods: {
    // Navigate to the previous page
    prevPage() {
      if (this.currentPage > 1) {
        this.currentPage--;
      }
    },
    // Navigate to the next page
    nextPage() {
      if (this.currentPage < this.totalPages) {
        this.currentPage++;
      }
    }
  }
};
</script>

<style>
.container {
  padding: 20px;
}

.item {
  margin-bottom: 10px;
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  margin: 0 5px;
  padding: 5px 10px;
  cursor: pointer;
}
</style>
